#reagendamento-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;

  .box{
    display: flex;
    flex-direction: column;
    width: 80%;
    max-width: 400px;
    height: 60%;
    max-height: 500px;
    background-color: #ffffff;
    padding: 10px;
    border: 0.5px solid black;
    border-radius: 10px;

    .header{
      all: unset;
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-size: 18px;
      font-family: 'Source Code Pro';
      font-weight: 500;
  
      .close{
        border-width: 0 0;
        align-items: center;
        justify-content: 'center';
        background-color: transparent;
      }
    }
  
    .previous{
      margin-top: 15px;
      height: 50px;
      
      .initialTitle{
        font-size: 13px;
        font-family: 'Source Code Pro';
        font-weight: 400;
      }
  
      .initialDate{
        font-size: 13px;
        font-family: 'Source Code Pro';
        font-weight: 500;
      }
  
    }
  
    .new{
  
      .newTitle{
        font-size: 13px;
        font-family: 'Source Code Pro';
        font-weight: 500;
      }
    }

    input{
      width: 100%;
    }
  
   .time{
      background-color: transparent;
      display: flex;
      height: 50%;
      max-height: 350px;
      width: 100%;
      margin-top: 10%;
      justify-content: center;
      flex-wrap: wrap;
      font-size: 15px;
      font-family: 'Source Code Pro';
      font-weight: 400;
      
      .timeStamp{
        display: flex;
        width: 55px;
        height: 20px;
        align-items: center;
        justify-content: center;
        text-align: center;
        vertical-align: middle;
        font-size: 12px;
        padding-top: 2px;
        border-radius: 10px;
        border: 1px solid #000000;
        margin-top: 5px;
        margin-bottom: 5px;
        margin-left: 5px;
        margin-right: 5px;
        color: #000000;
      }
      
      .timeStamp-selected{
        background-color: #B09360;
      }
  
    }
    .saveBtn{
      align-self: center;
      width: 100%;
      margin-top: 35px;
    }
  }
}