#forgot-container{
  display: flex;
  flex-direction: column;
  height: 100%;
  align-items: center;
  
  outline: 0;
  margin: 0 auto;

  .imgContainer{
    display: flex;
    justify-content: center;
    height: 20%;
    margin-top: 15px;
    
    img {
      height: 100%;
      min-height: 100px;
    }
  } 

  .formDiv {
    width: 90%;
    margin-top: 100px;

    .forgotText{
      margin: 0 auto;
      width: 90%;
      text-align: center;
      color: gray;
      font-size: 14px;
      font-family: 'Source Code Pro';
      font-weight: 400;
    }

    form{
      height: 100%;
      align-items: center;
      display: flex;
      flex-direction: column;      

      input{
        margin-top: 100px;
      }

      button{
        margin-top: 100px;
      }
    }
  }

}