#editprofile-container{
  display: flex;
  flex-direction: column;
  height: 100%;
  outline: 0;
  margin: 0 auto;
  align-items: center;

  .formDiv {

    width: 90%;
    margin-top: 10%;
 
    form{
      height: 100%;
      align-items: center;
      display: flex;
      flex-direction: column;
      
      input{
        margin-top: 20px;
      } 

      button {
        margin-top: 200px;
      }

      .fixedValueCpf{
        margin-bottom: 5%;  
        font-family: 'Source Code Pro';
        font-weight: 500;
        letter-spacing: 0.5px;
      }
    

      .whatsapp{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        margin-top: 100px;
        font-size: 15px;
        font-family: 'Source Code Pro';
        font-weight: 400;

        .check{
          margin-top: 2px;
          width: 15px;
          height: 15px;
          margin-right: 10px;
          border: 2px solid #000000;
          background-color: #000000;
        }
      }

      .email{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        margin-top: 100px;
        font-size: 12px;
        font-family: 'Source Code Pro';
        font-weight: 400;

        .check{
          margin-top: 2px;
          width: 15px;
          height: 15px;
          margin-right: 10px;
          border: 2px solid #000000;
          background-color: #000000;
        }
      }
    }
  }
}