.alunoItem{
  width: 75%;
  max-width: 400px; 
  border: 2px solid #B09360;
  border-radius: 10px;
  margin-top: 10px;
  
  .header{
    background-color: #B09360;
  }

  .header{
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 10px;
    padding-right: 10px;
    letter-spacing: 0.5px;
    border-radius: 5px 5px 5px 5px;
    color: #faffff;
    
    
    .headerValue{
      font-size: 14px;
      letter-spacing: 0.5px;
      font-family: 'Source Code Pro';
      font-weight: 500;
      text-align: center;
    }
  }

  .body{
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.5s ease;
    width: 100%;
    display: flex;
    flex-direction: column;
    background-color: transparent;
    font-family: 'Source Code Pro';
    font-weight: 400;
    font-size: 12px;

    .aluno-view{
      padding-top: 10px;
      padding-left: 10px;
      padding-right: 10px;
      .email{

      }
      .phone{
        margin-top: 8px;
      }
    }

    .course-container{
      padding-left: 8px;
      padding-right: 8px;
      padding-bottom: 5px;
      .course-view{
        display: flex;
        justify-content: space-between;
        margin-top: 7px;
        border-width: 0 0 1px 0;
        border-style: solid;
        border-color: #d3d3d3;
        border-radius: 2px;
        padding-right: 3px;
        padding-left: 3px;
  
        .course-info{
          display: flex;
          flex-direction: column;
          width: 100%;
          justify-content: space-evenly;
  
          .course-data{
            display: flex;
            justify-content: space-between;
            padding-right: 10px;
            margin-top: 5px;
          }
        }
  
        .btn-view{
          display: flex;
          flex-direction: column;
          padding-top: 3px;
          
          .cursoBtn{
            width: 100%;
            padding-top: 5px;
            padding-bottom: 4px;
            background-color: transparent;
            outline: 0;
            border-width: 0 0;
            align-items: center;
            justify-content: center;
          }
        }
      }
    }
  }

  .body.bodyOpen{
    width: 100%;
    background-color: transparent;
    height: auto;
    transition: max-height 0.5s ease;
    max-height: 1000px;
    display: flex;
    flex-direction: column;
  }
}

.alunoItemOpen{ 
  background-color: #faffff;
  

  .header{
    border-radius: 5px 5px 0 0;
  }
}