#registrocurso-container{
  display: flex;
  flex-direction: column;
  //width: 100%;
  height: 100%;
  outline: 0;
  margin: 0 auto;
  align-items: center;

  
  .imgContainer{
    display: flex;
    justify-content: center;
    height: 20%;
    margin-top: 15px;

    img {
      height: 100%;
      min-height: 100px;
    }
  } 

  .formDiv {
    display: flex;
    flex-direction: column;
    width: 90%;
    margin-top: 10%;
    height: 65%;
    align-items: center;
    
    form{
      width: 100%;
      max-width: 450px;
      align-items: center;
      display: flex;
      flex-direction: column; 

      input{
        margin-top: 20px;
      }

      textarea{
        margin-top: 25px;
        resize: none;
      }
      .textarea-count{
        margin-right: 15px;
        align-self: flex-end;
        margin-top: 2px;
        font-size: 12px;
        color: gray;
        font-family: 'Source Code Pro';
        font-weight: 400;
      }

      button{
        margin-top: 100px;
      }
    }
  }
}