#header-container{
  width: 100%;
  display: flex;
  background: #faffff;
  align-items: center;
  justify-content: space-between;
  padding-left: 10px;
  padding-right: 10px;
  border-bottom: solid 1px silver;
  padding-top: 7px;
  padding-bottom: 7px;

  button{
    background-color: #faffff;
    align-items: center;
    justify-content: center;
    border: 0;
  }
  
  h3{
    font-size: 18px;
    font-family: 'Source Code Pro';
    font-weight: 500;
  }

  .imgHeader{
    height: 25px;

    img{
      height: 100%;
    }
  }
}