.novoAlunoCard{
  width: 75%;
  max-width: 400px; 
  border: 2px solid #B09360;
  border-radius: 10px;
  margin-top: 10px;

  .header, .headerInfo, .headerValue{
    background-color: #B09360;
  }

  .header{
    width: 100%;
    justify-content: center;
    text-align: center;
    font-family: 'Source Code Pro';
    font-weight: 500;
    letter-spacing: 0.5px;
    border-radius: 5px 5px 5px 5px;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 10px;
    padding-right: 10px;
    color: #faffff;
    
    .headerInfo{
      display: flex;
      justify-content: space-between;

      .headerValue{
        font-size: 10px;
        margin-top: 2px;
      }
    }
  }

  .body{
    max-height: 0;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    transition: max-height 0.5s ease;

    .data{
      width: 100%;
      padding-top: 10px;
      padding-bottom: 10px;
      padding-left: 10px;
      padding-right: 10px;
      font-family: 'Source Code Pro';
      font-weight: 500;
  
      .dataInfo{
        display: flex;
        justify-content: space-between;
  
        .dataValueLeft{
          text-align: left;
          font-size: 13px;
          margin-top: 8px;
        }
        .dataValueRight{
          text-align: right;
          font-size: 13px;
          margin-top: 8px;
        }
      }
    }
  
    .aprovar{
      display: flex;
      align-items: center;
      justify-content: space-around;
      padding-left: 5px;
      padding-right: 5px;
      margin-top: 10px;
      margin-bottom: 10px;
      font-family: 'Source Code Pro';
      font-weight: 400;
  
      .aprovarText{
        font-size: 12px;
        color: gray;
      }
  
      button{
        margin: unset;
        font-family: 'Source Code Pro';
        font-weight: 400;
        letter-spacing: 1px;
        font-size: 10px;
        color: #FaFFFF;
        height: 25px;
        width: 20%;
        border-radius: 10px;
        background-color: #B09360;
        border: none;
      }
    }

  }

  .body.bodyOpen{
    height: auto;
    transition: max-height 0.5s ease;
    max-height: 200px;
    display: flex;
    flex-direction: column;
  }

}

.novoAlunoCardOpen{ 
  background-color: #faffff;
  

  .header{
    border-radius: 5px 5px 0 0;
  }
}
