#login-container{
  display: flex;
  flex-direction: column;
  height: 100%;
  margin: 0 auto;
  align-items: center;
  background-image: url('../../assets/sofieto-background.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: right;


  .imgContainer{
    display: flex;
    justify-content: center;
    height: 30%;
    margin-top: 70px;
    
    img {
      height: 50%;
      min-height: 200px;
    }
  }

  .formDiv {

    width: 90%;
    margin-top: 100px;
    
    
    form{
      align-items: center;
      display: flex;
      flex-direction: column;
      
      input{
        margin-top: 20px;
        color: #FFFFFF;
      }

      button{
        background-color: #B09360 !important;
        margin-top: 30px;
      }
    }
  }

  .forgot{
    width: 50%;
    margin: 0 auto;
    margin-top: 30px;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-family: 'Source Code Pro';
    font-weight: 400;
    text-decoration: none;
    color: #FFFFFF;
    font-size: 14px;
  }

  .link {
    color: #FFFFFF;
    font-weight: 900;
    text-decoration: none;
  }
  
  .bottom {
    color: #FFFFFF;
    bottom: 0%;
    margin-top: 50px;
    font-family: 'Source Code Pro';
    font-weight: 400;
    font-size: 14px;
  }
}