#alterhorario-container{
  display: flex;
  flex-direction: column;
  height: 100%;
  align-items: center;
  outline: 0;
  margin: 0 auto;

  .title{
    margin-top: 50px;
    font-size: 14px;
    font-family: 'Source Code Pro';
    font-weight: 500;
  }

  .formDiv {
    display: flex;
    flex-direction: column;
    width: 90%;
    margin-top: 40px;
    align-self: center;
    
    form{
      height: 100%;
      align-items: center;
      display: flex;
      flex-direction: column;

      .infoView {
        display: flex;
        flex-direction: column;
        font-size: 13px;
        font-family: 'Source Code Pro';
        font-weight: 500;
        align-items: center;
        padding-top: 3px;
        padding-bottom: 3px;

        .infoData{
          margin-top: 2px;
        }


      }

      .weekday{
        display: flex;
        width: 100%;
        height: 80px;
        margin-top: 20px;
        align-items: center;
        justify-content: center;
        font-size: 15px;
        font-family: 'Source Code Pro';
        font-weight: 400;
  
        .dayItem{
          display: flex;
          width: 45px;
          height: 50px;
          border: 1px solid #000000;
          border-radius: 8px;
          align-items: center;
          justify-content: center;
          text-align: center;
          margin-left: 5px;
          margin-right: 5px;
          color:#000000;
        }

        .dayItem-selected{
          background-color: #B09360;
        }
      }
  

      .time{
        display: flex;
        width: 95%;
        margin-top: 20px;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        font-size: 15px;
        font-family: 'Source Code Pro';
        font-weight: 400;
        
        .timeStamp{
          display: flex;
          width: 55px;
          height: 20px;
          align-items: center;
          justify-content: center;
          text-align: center;
          vertical-align: middle;
          font-size: 12px;
          border-radius: 10px;
          border: 1px solid #000000;
          margin-top: 5px;
          margin-bottom: 5px;
          margin-left: 5px;
          margin-right: 5px;
          color: #000000;
        }
        
        .timeStamp-selected{
          background-color: #B09360;
        }
      }

      button{
        margin-top: 200px !important;
      }
    }
  }

}