
#reset-container{
  display: flex;
  flex-direction: column;
  height: 100%;
  align-items: center;
  
  outline: 0;
  margin: 0 auto;

  .headerReset{
    width: 100%;
    display: flex;
    height: 40px;
    background: #faffff;
    align-items: center;
    justify-content: space-between;
    padding-left: 10px;
    padding-right: 10px;
    border-bottom: solid 1px silver;
    outline: 0;
  
    h3{
      font-size: 18px;
      font-family: 'Source Code Pro';
      font-weight: 500;
      align-self: center;
      margin: 0 auto;
    }
  }

  .imgContainer{
    display: flex;
    justify-content: center;
    height: 20%;
    margin-top: 15px;
    
    img {
      height: 100%;
      min-height: 100px;
    }
  } 

  .formDiv {
    width: 90%;
    margin-top: 100px;

    .forgotText{
      margin: 0 auto;
      width: 90%;
      text-align: center;
      color: gray;
      font-size: 14px;
      font-family: 'Source Code Pro';
      font-weight: 400;
    }

    form{
      height: 100%;
      align-items: center;
      display: flex;
      flex-direction: column;      

      input{
        margin-top: 30px;
      }
      button{
        margin-top: 150px;
      }
    }
  }

}