#horasadm-container{
  display: flex;
  flex-direction: column;
  height: 100%;
  outline: 0;
  margin: 0 auto;
  align-items: center;

  .titleContainer{
    display: flex;
    flex-direction: column;
    margin-top: 40px;
    margin-bottom: 40px;
    align-items: center;    

    .title{
      margin-top: 5px;
      font-size: 14px;
      font-family: 'Source Code Pro';
      font-weight: 500;
    }
  }

  .weekday{
    display: flex;
    width: 100%;
    max-width:450px;
    height: 80px;
    margin-top: 20px;
    align-items: center;
    justify-content: space-evenly;
    font-size: 15px;
    font-family: 'Source Code Pro';
    font-weight: 400;

    .dayItem{
      display: flex;
      width: 45px;
      height: 50px;
      border: 1px solid #000000;
      border-radius: 8px;
      align-items: center;
      justify-content: center;
      text-align: center;
      color:#000000;
    }

    .dayItem-selected{
      background-color: #B09360;
    }
  }

  .buttons{
    display: flex;
    margin-top: 20px;
    justify-content: space-between;
    max-width: 350px;
    width: 90%;

    .btnHr{
      display: flex;
      background-color: #B09360;
      justify-content: space-between;
      color: #FFFFFF;
      border: 0;
      outline: 0;
      font-size: 14px;
      font-family: 'Source Code Pro';
      font-weight: 400;
      border: 1px solid #B09360;
      padding-top: 5px;
      padding-bottom: 5px;
      padding-left: 10px;
      padding-right: 10px;
      border-radius: 5px;
    }

    .editando{
      background-color: #faffff;
      color: #B09360;
    }
  }

  .horarios{
    display: flex;
    width: 70%;   
    width: 350px;
    height: 300px;
    align-content: center;
    align-items: center;
    justify-content: space-evenly;
    flex-wrap: wrap;
    font-size: 15px;
    font-family: 'Source Code Pro';
    font-weight: 400;

    .editStamp{
      display: flex;
      width: 95px;
      height: 20px;
      justify-content: space-between;
      align-items: center;
      padding-left: 3px;
      padding-right: 3px;
      margin: 10px;
      background-color: #000000;
      font-size: 12px;
      border-radius: 10px;
      border: 2px solid #000000;

      .icon{
        padding: 0;
        outline: 0;
      }

      .editTime{
        width: 55px;
        justify-content: center;
        text-align: center;
        padding-top: 2px;
        font-weight: 400;
        font-size: 12px;
        border-radius: 10px;
        background-color: #FFFFFF;
      }
    }
    .timeStamp{
      display: flex;
      width: 55px;
      height: 20px;
      align-items: center;
      justify-content: center;
      text-align: center;
      vertical-align: middle;
      margin: 10px;
      font-size: 12px;
      border-radius: 10px;
      border: 1px solid #000000;
      color:#000000;
    }
    .timeStamp-selected{
      background-color: #B09360;
      border: 1px solid #000000;
    }
    .timeStamp-deactivated{
      opacity: 0.5;
      border: 0.5px solid gray;
    }
  }

  .save{
    align-self: center;
  }
  .off{
    opacity: 0.5;
  }
  
}
