  
.details{
  display: flex;
  flex-direction: column;
  align-items: center;
  color: gray;
  font-size: 15px;
  font-family: 'Source Code Pro';
  font-weight: 400;
  width: 95%;
  margin-bottom: 12px;
  border-radius: 10px;
  box-shadow: rgba(112, 112, 112, 48) 0px 0px 10px;
  
  .detailsItem{
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 10px;
    
    .cursoText{
      color: #000000;
      font-weight: 900;
    }
    .hourText{
      margin-top: 3px;
      color: #808080;
    }
  }

  .editBtn{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2px;
    border-radius: 20px;
  }

  .reschedule{
    display: flex;
    flex-direction: column;
    width: 100%;
    text-align: center;
    border-width: 1px 0 0 0;
    border-style: solid;
    border-color: #c0c0c0;
    color: #000000;
    font-weight: 900;
  }
}