.cursoItem{
  width: 75%;
  max-width: 500px;
  margin-top: 10px;
  margin-bottom: 10px;
  border-radius: 10px;
  border-width: 1px;
  border-style: solid;
  border-color: #6f706e;
  -webkit-transition: border-color 1s ease-out;
  -moz-transition: border-color 1s ease-out;
  -o-transition: border-color 1s ease-out;
  transition: border-color 1s ease-out;

  .header{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: center;
    width: 100%;
    padding-top: 5px;
    padding-bottom: 10px;
    padding-left: 10px;
    padding-right: 10px;
    border-radius: 8px 8px 0 0;
    color: white;
    background-color: #6f706e;
    -webkit-transition: background-color 1s ease-out;
    -moz-transition: background-color 1s ease-out;
    -o-transition: background-color 1s ease-out;
    transition: background-color 1s ease-out;
    
    .headerValue{
      font-family: 'Source Code Pro';
      font-weight: 400;
      font-size: 12px;
      letter-spacing: 0.5px;
    }

    .headerInfo{
      display: flex;
      justify-content: space-between;
      margin-top: 5px;
      
      .headerCourse{
        font-family: 'Source Code Pro';
        font-weight: 500;
        font-size: 13px;
        letter-spacing: 0.5px;
      }

      .headerBtn{


        .switch {
          position: relative;
          display: inline-block;
          width: 38px;
          height: 18px;
        }
        
        .switch input { 
          opacity: 0;
          width: 0;
          height: 0;
        }
        
        .slider {
          position: absolute;
          cursor: pointer;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          background-color: #ccc;
          -webkit-transition: .4s;
          transition: .4s;
        }
        
        .slider:before {
          position: absolute;
          content: "";
          height: 16px;
          width: 16px;
          left: 2px;
          bottom: 1px;
          background-color: white;
          -webkit-transition: .4s;
          transition: .4s;
        }
        
        input:checked + .slider {
          background-color: #2196F3;
        }
        
        input:focus + .slider {
          //box-shadow: 0 0 1px #2196F3;
        }
        
        input:checked + .slider:before {
          -webkit-transform: translateX(17px);
          -ms-transform: translateX(17px);
          transform: translateX(17px);
        }
        
        /* Rounded sliders */
        .slider.round {
          border-radius: 34px;
        }
        
        .slider.round:before {
          border-radius: 50%;
        }

      }
    }
  }

  .infoView{
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
    display: flex;
    justify-content: space-between;
    background-color: transparent;
    font-family: 'Source Code Pro';

    .dataView{
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }

    .dataLabel{
      font-weight: 500;
    }

    .descLabel{
      margin-top: 5px;
      font-weight: 500;
    }

    .dataValue{
      overflow-wrap: break-word;
      margin-left: 5px;
      margin-right: 5px;
    }
    .data {
      display: flex;      
    }
  }

  .desc{
    font-family: 'Source Code Pro';
    font-weight: 400;
    font-size: 12px;
    padding-left: 10px;
    padding-right: 10px;
  }

  .btnContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    .cursoBtn{
      width: 25px;
      padding-top: 3px;
      padding-bottom: 3px;
      background-color: transparent;
      outline: 0;
      border-width: 0 0;
      align-items: center;
      justify-content: center;
    }
  }
     
  .headerActive{
    background-color: #B09360;
  }
}

.cursoItemActive{
  border-color: #B09360;
}