#homeadm-container{
  display: flex;
  list-style: none;
  flex-direction: column;
  height: 100%;
  outline: 0;
  margin: 0 auto;
  align-items: center;

  .testeClass, .testeClass > abbr{
    background: #B09360;
  }

  .events{
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 500px;   
    height: 65%;
    padding-top: 15px;

    .currentDate{
      display: flex;
      flex-direction: column;
      font-family: 'Source Code Pro';
      font-weight: 900;
      width: 25%;
      color: #000000;
      
      .monthDay{
        text-align: center;
        font-size: 27px;
      }
      .weekDay{
        text-align: center;
        font-size: 18px;
      }
    }
    
    .eventsDate{
      width: 75%;
    }
  }
  
}
