.cursoAlunoCard{
  width: 75%;
  max-width: 400px;
  border: 2px solid #B09360;
  border-radius: 10px;
  margin-top: 10px;

  .header, .headerInfo, .headerValue{
    background-color: #B09360;
  }

  .header{
    width: 100%;
    justify-content: center;
    text-align: center;
    font-family: 'Source Code Pro';
    font-weight: 500;
    letter-spacing: 0.5px;
    border-radius: 5px 5px 5px 5px;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 10px;
    padding-right: 10px;
    color: #ffffff;
    
    .headerInfo{
      display: flex;
      justify-content: space-between;

      .headerValue{
        font-size: 10px;
        font-weight: 900;
        margin-top: 2px;
      }
    }
  }

  .body{
    max-height: 0;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    transition: max-height 0.5s ease;

    .data{
      display: flex;
      justify-content: space-between;
      padding-top: 10px;
      padding-bottom: 10px;
      padding-left: 10px;
      padding-right: 10px;
      font-family: 'Source Code Pro';
      font-weight: 500;
  

      .content{
        
        padding-right: 10px;
        width: 90%;

        .dataInfo{
          display: flex;
          justify-content: space-between;
    
          .dataValue{
            font-size: 13px;
            margin-top: 8px;
          }
        }
      }

      .btn-view{
        display: flex;
        flex-direction: column;
        margin-top: 5px;
        
        .cursoBtn{
          width: 100%;
          padding-top: 4px;
          padding-bottom: 4px;
          background-color: transparent;
          outline: 0;
          border-width: 0 0;
          align-items: center;
          justify-content: center;
        }
      }
      
    }
  
    .desc{
      font-family: 'Source Code Pro';
      font-weight: 400;
      font-size: 14px;
      padding-left: 10px;
      padding-right: 10px;
      margin-bottom: 5px;

      .dataValue{
        overflow-wrap: break-word;
        margin-left: 5px;
        margin-right: 5px;
      }
    }
  }

  .body.bodyOpen{
    height: auto;
    transition: max-height 0.5s ease;
    max-height: 200px;
    display: flex;
    flex-direction: column;
  }
}

.cursoAlunoCardOpen{ 
  background-color: #faffff;

  .header{
    border-radius: 5px 5px 0 0;
  }
}
