#detalhescurso-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;

  .box{
    display: flex;
    flex-direction: column;
    width: 80%;
    max-width: 400px;
    background-color: #ffffff;
    padding: 10px;
    border: 0.5px solid black;
    border-radius: 10px;

    .header{
      all: unset;
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-size: 22px;
      font-family: 'Source Code Pro';
      font-weight: 500;
  
      .close{
        background-color: transparent;
        border-width: 0 0;
        align-items: center;
        justify-content: 'center';
      }
    }
  
    .titulocurso{
      margin-top: 20px;
      font-size: 18px;
      font-family: 'Source Code Pro';
      font-weight: 500;
    }
  
    .qtdClass{
      margin-top: 20px;
      font-family: 'Source Code Pro';
      font-weight: 500;
    }
  
    .desc{
      margin-top: 20px;
      font-family: 'Source Code Pro';
      font-weight: 500;
  
      .descText{
        overflow-wrap: break-word;
        margin-top: 10px;
        font-family: 'Source Code Pro';
        font-weight: 300;
        font-size: 14px;
      }
    }

  }
}