#editaviso-container{
  display: flex;
  flex-direction: column;
  height: 100%;
  outline: 0;
  margin: 0 auto;
  align-items: center;

  .avisos-label{
    display: flex;
    flex-direction: column;
    font-family: 'Source Code Pro';
    font-weight: 500;
    font-size: 18px;
    margin-top: 30px;
    padding-left: 10px;
    padding-right: 10px;
    align-items: flex-start;
    justify-content: center;
    width: 90%;
    max-width: 400px;
  }

  .avisos-text{
    display: flex;
    flex-direction: column;
    margin-top: 30px;
    width: 90%;
    max-width: 400px;
    align-items: center;
    justify-content: 'center';

    textarea{
      resize: none;
    }

    .textarea-count{
      margin-right: 15px;
      align-self: flex-end;
      margin-top: 2px;
      font-size: 12px;
      font-family: 'Source Code Pro';
      font-weight: 400;
      color: gray;
    }
  }
}