#editarhorario-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;

  .box{

    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Source Code Pro';
    font-weight: 500;
    padding: 20px;
    border-width: 1px;
    border-style: solid;
    border-color: gray;
    border-radius: 10px;
    background-color: #ffffff;

    .novo-horario{
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 100%;
      margin-top: 20px;
      font-size: 14px;

      input{
        margin-top: 10px;
        align-self: center;
        width: 60%;
        text-align: center;
        
        
      }
    }

    .btnContainer{
      display: flex;
      margin-top: 30px;
      justify-content: space-evenly;
      align-items: center;
      width: 100%;

      .cancelar{
        background-color: #817f7f;
      }

      .confirmar{
        background-color: #000000;
      }

      button{
        outline: 0;
        padding: 7px;
        border-width: 0;
        border-radius: 3px;
        color: #ffffff;
        font-family: 'Source Code Pro';
        font-weight: 400;
      }
    }
  }
}