.drawerComp{
  height: 100%;
  width: 100%;
  background-color: black;
  opacity: 0.5;
  transition: all 0.2s ease-in-out;
  transform: translate(-100%);
  position: absolute;
}

.drawerCompOpen {
  transform: translate(0);
}

.menu {
  height: 100%;
  width: 200px;
  opacity: 1;
  background-color: #f9f9f9;
  background-image: url('../../assets/sofieto-background.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: left;
  transition: all 0.2s ease-in-out;
  transform: translate(-140%);
  align-self: flex-start !important;
  position: absolute;

  .header{
    color: #faffff;
    display: flex;
    height: 40px;
    justify-content: center;
    align-items: center;

    h3{
      font-size: 18px;
      font-family: 'Source Code Pro';
      font-weight: 500;
    }
  }

  img {
    width: 120px;
    height: 120px;
    align-self: center;
    margin-top: 20px;
    margin-bottom: 20px;
    margin-left: 30px;
  }

  .userInfo{
    align-items: center;

    .btnProfile{
      display: flex;
      flex-direction: column;
      background-color: transparent;
      padding-top: 10px;
      padding-left: 10px;
      height: 70px;
      width: 100%;
      outline: 0;
      border-width: 0 0;
      color: #faffff;

      .title{
        font-size: 14px;
        font-family: 'Source Code Pro';
        font-weight: 500;
      }
      
      .userName{
        margin-top: 3px;
        font-size: 14px;
        font-family: 'Source Code Pro';
        font-weight: 400;
      }
    }
  }

  .listaMenu{

    .menuItem {
      display: flex;
      height: 40px;
      align-items: center;
      padding-left: 20px;
      color: #faffff;
      font-family: 'Source Code Pro';
      font-weight: 300;
      text-decoration: none;
    }
  }

  .sair{
    position: absolute;
    height: 40px;
    display: flex;
    bottom: 5px;
    padding-left: 10px;
    width: 100%;
    align-items: center;
    text-decoration: none;
    //align-self: baseline;
    color: #faffff;
    font-family: 'Source Code Pro';
    font-weight: 300;
  
    .icon{
      margin-right: 10px;
    }
  }
}

.openMenu{
  transform: translate(0);
}

