.eventCard{
  display: flex;
  width: 95%;
  margin-bottom: 12px;
  margin-left: 5px;
  justify-content: space-between;
  border-radius: 10px;
  box-shadow: rgba(112, 112, 112, 48) 0px 0px 10px;
  color: #141414;
  padding-left: 5px;

  .eventInfo{
    width: 100%;
    padding: 5px;
    font-family: 'Source Code Pro';
    .student{
      font-size: 16px;
      font-weight: 500;
    }
    .course{
      font-size: 14px;
      color: gray;
      font-weight: 400;
    }
    .title{
      font-size: 14px;
      color: gray;
      font-weight: 400;
    }
    .time{
      font-size: 16px;
      font-weight: 500;
    }
  }

  .reagendar{
    display: flex;
    align-items: center;
    justify-content: center;
    outline: 0;
    background-color: transparent;
    border: 0;
  }

  .attendance{
    display: flex;
    flex-direction: column;
    border-radius: 0 10px 10px 0;

    button{
      outline: 0;
      background-color: transparent;
      width: 30px;
      justify-content: center;
      align-items: center;
      height: 50%;
    }

    .accept{
      border-radius: 0 10px 0 0;
      border-width: 0 0 0.5px 1px;
      border-style: solid;
      border-color: silver;
    }
    .attTrue{
      background-color: #057dcd;
    }
    .refuse{
      border-radius: 0 0 10px 0;
      border-width: 0.5px 0 0 1px;
      border-style: solid;
      border-color: silver;
    }
    .attFalse{
      background-color: #ff0000;
    }

  }
}