#cadastro-container {
  display: flex;
  flex-direction: column;
  height: 100%;
  outline: 0;
  margin: 0 auto;
  align-items: center;

  
  .imgContainer{
    display: flex;
    justify-content: center;
    height: 20%;
    margin-top: 15px;

    img {
      height: 100%;
      min-height: 100px;
    }
  }  

  .formDiv {
    display: flex;
    flex-direction: column;
    width: 90%;
    margin-top: 35px;
    align-self: center;

    
    .formBody{
      height: 100%;
      align-items: center;
      display: flex;
      flex-direction: column;

      .courseDetails{
        display: flex;
        align-items: center;
        margin-top: 20px;
        font-size: 13px;
        font-family: 'Source Code Pro';
        font-weight: 500;
        text-decoration: underline;
        letter-spacing: 0.5px;

        .icon{
          margin-right: 5px;
        }
      }
      
      input, select{
        margin-top: 20px;
      }

      .weekday{
        display: flex;
        width: 100%;
        height: 80px;
        margin-top: 10px;
        align-items: center;
        justify-content: center;
        font-size: 15px;
        font-family: 'Source Code Pro';
        font-weight: 400;
  
        .dayItem{
          display: flex;
          width: 45px;
          height: 50px;
          border: 1px solid #000000;
          border-radius: 8px;
          align-items: center;
          justify-content: center;
          text-align: center;
          margin-left: 5px;
          margin-right: 5px;
          color:#000000;
        }

        .dayItem-selected{
          background-color: #B09360;
        }
      }

      .time{
        display: flex;
        width: 95%;
        margin-top: 20px;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        font-size: 15px;
        font-family: 'Source Code Pro';
        font-weight: 400;
        
        .timeStamp{
          display: flex;
          width: 55px;
          height: 20px;
          align-items: center;
          justify-content: center;
          text-align: center;
          vertical-align: middle;
          font-size: 12px;
          border-radius: 10px;
          border: 1px solid #000000;
          margin-top: 5px;
          margin-bottom: 5px;
          margin-left: 5px;
          margin-right: 5px;
          color: #000000;
        }
        
        .timeStamp-selected{
          background-color: #B09360;
        }
      }

      .whatsapp{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        margin-top: 100px;
        font-size: 15px;
        font-family: 'Source Code Pro';
        font-weight: 400;

        .check{
          margin-top: 2px;
          width: 15px;
          height: 15px;
          margin-right: 10px;
          border: 2px solid #000000;
          background-color: #000000;
        }
      }

      .email{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        margin-top: 100px;
        font-size: 15px;
        font-family: 'Source Code Pro';
        font-weight: 400;

        .check{
          margin-top: 2px;
          width: 15px;
          height: 15px;
          margin-right: 10px;
          border: 2px solid #000000;
          background-color: #000000;
        }
      }
    }
  }

  .bottom {
    margin-top: 55px;
    text-align: center;
    font-family: 'Source Code Pro';
    font-weight: 400;
    font-size: 14px;
  
    .link {
      color: #000000;
      font-family: 'Source Code Pro';
      font-weight: 900;
      text-decoration: none;
    } 
  }
}
