#horarios-container{
  display: flex;
  flex-direction: column;
  height: 100%;
  outline: 0;
  margin: 0 auto;
  align-items: center;

  .titleContainer{
    display: flex;
    flex-direction: column;
    margin-top: 30px;
    margin-bottom: 30px;
    align-items: center;    

    .title{
      margin-top: 5px;
      font-size: 14px;
      font-family: 'Source Code Pro';
      font-weight: 500;
    }
  }

  .daysContainer{
    display: flex;
    width: 100%;
    max-width: 500px;
    margin-top: 15px;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-evenly;

    .dayBox{
      display: flex;
      flex-direction: column;
      height: 150px;
      margin-top: 50px;
      justify-content: space-between;
      align-items: flex-end;

      .label{
        margin-right: 3px;
        font-size: 10px;
        font-family: 'Source Code Pro';
        font-weight: 400;
      }

      .value{
        width: 80%;
        display: flex;
        justify-content: space-between;
        align-items: center;


        input{
          width: 30px;
          text-align: center;
        }
      }

      .fakeItem{
        width: 45px;
        height: 50px;
        color: white;
        border-radius: 8px;
        padding-top: 12px;
        align-self: center;
        text-align: center;
      }

      .dayItem{
        display: flex;
        width: 45px;
        height: 50px;
        border: 1px solid #000000;
        border-radius: 8px;
        align-items: center;
        justify-content: center;
        text-align: center;
        margin-left: 5px;
        margin-right: 5px;
        color:#000000;
        font-family: 'Source Code Pro';
        font-weight: 400;
      }
  
      .dayItem-selected{
        background-color: #B09360;
      }
    }

  }
  .save {
    margin-top: 100px;
    align-self: center;
  }
}