#alunocurso-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;

  .box{

    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Source Code Pro';
    font-weight: 400;
    font-size: 14px;
    padding: 20px;
    border-width: 1px;
    border-style: solid;
    border-color: gray;
    border-radius: 10px;
    background-color: #ffffff;

    .details-curso{
      margin-bottom: 15px;
      font-weight: bold;
    }

    .delete-aluno{
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      margin-top: 20px;
      font-size: 12px;

      .check{
        margin-top: 2px;
        width: 15px;
        height: 15px;
        margin-right: 20px;
        font-weight: bold;
        border: 2px solid black;
        background-color: black;
      }
    }

    .btnContainer{
      display: flex;
      margin-top: 20px;
      justify-content: space-evenly;
      align-items: center;
      width: 100%;

      .cancelar{
        background-color: #817f7f;
      }

      .confirmar{
        background-color: #000000;
      }

      button{
        outline: 0;
        padding: 7px;
        border-width: 0;
        border-radius: 3px;
        color: #ffffff;
        font-family: 'Source Code Pro';
        font-weight: 400;
      }
    }
  }
}